import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ContractApiService } from 'src/app/core/services/contact.services/contract.api.service';
import { JsonService } from 'src/app/core/services/json.services/json.service';
import { TransactionFormComponent } from '../transaction.form.component copy/transaction.form.component';

@Component({
  selector: 'app-dynamic-form',
  templateUrl: './dynamic.form.component.html',
  styleUrls: ['./dynamic.form.component.scss']
})
export class DynamicFormComponent implements OnInit {
  t;
  constructor(public dialog: MatDialog, private json: JsonService, private contract: ContractApiService) {
  }
  ngOnInit(): void {
    this.reChoiseTransaction();
  }
  public reChoiseTransaction(): void {
    this.t = {
      account: '',
      name: '',
      expiration: '',
      body: [],
      authorizationAlly: [{ accounts: [] }]
    };
    this.dialog.open(TransactionFormComponent, {
      width: '250px',
    }).afterClosed().subscribe(result => {
      this.contract.getFromJsonTemplate().subscribe((j: any[]) => {
        if (j.find(s => s.name === result.selectedAction.name)) {
          this.t.body.push({
            contractName: result.contract,
            selectedAction: j.find(s => s.name === result.selectedAction.name),
            actions: result.actions,
            structs: result.structs,
            authorizations: [
              {
                name: 'from',
                permission: 'active'
              }
            ],
          });
        }
        else {
          this.t.body.push({
            contractName: result.contract,
            selectedAction: result.selectedAction,
            actions: result.actions,
            structs: result.structs,
            authorizations: [
              {
                name: 'from',
                permission: 'active'
              }
            ],
          });
        }
      })
    });
  }
  public actionStructure(name: string, tId: number): any {
    return this.t.body[tId].structs.find(item => item.name === name);
  }
  public async import() {
    this.json.importObjectFromJson((e) => {
      var reader = new FileReader();
      reader.onload = (e2 => {
        const obj = JSON.parse(e2.target.result.toString());
        this.t = obj.t;
        this.contract.changeApi(obj.a);
      });
      reader.readAsText(e.target.files[0]);
    });
  }
  public export() {
    this.json.exportObjectAsJson(
      {
        t: this.t,
        a: this.contract.getApiIndex().split(':: ')[1]
      }
      , 'msig-save-' + new Date().toLocaleString());
  }
  public addAuthorization(tId: number): void {
    console.log(this.t);
    this.t.body[tId].authorizations.push(
      {
        name: 'from',
        permission: 'active'
      }
    );
  }
  public removeAuthorization(tId: number, i: number): void {
    this.t.body[tId].authorizations = this.t.body[tId].authorizations.filter((x, y) => y !== i);
  }
  public removeAuthorizationAlly(tId: number, i: number): void {
    this.t.authorizationAlly[tId].accounts = this.t.authorizationAlly[tId].accounts.filter((x, y) => y !== i);
  }
  public addAuthorizationAlly(tId: number): void {
    this.t.authorizationAlly[tId].accounts.push({
      name: 'from',
      permission: 'active'
    });
  }
  public onFromChange(from: string, tId: number) {
    this.contract.getProducersTEST(from).subscribe(info => {
      let oldLength = this.t.authorizationAlly[tId].accounts.length;
      info.permissions.forEach(p => {
        if (p.auth.accounts.length > 0) {
          p.auth.accounts.forEach(a => {
            if (this.t.authorizationAlly[tId].accounts.filter(l => l.name === a.actor && l.permission === a.permission).length === 0) {
              this.t.authorizationAlly[tId].accounts.push({
                name: a.actor,
                permission: a.permission
              });
            }
          })
        }
      })
      if (oldLength === this.t.authorizationAlly[tId].accounts.length) {
        this.t.authorizationAlly[tId].accounts = []
      }
    });
  }
  public addTransaction() {
    this.dialog.open(TransactionFormComponent, {
      width: '250px',
    }).afterClosed().subscribe(result => {
      this.contract.getFromJsonTemplate().subscribe((j: any[]) => {
        if (j.find(s => s.name === result.selectedAction.name)) {
          this.t.body.push({
            contractName: result.contract,
            selectedAction: j.find(s => s.name === result.selectedAction.name),
            actions: result.actions,
            structs: result.structs,
            authorizations: [
              {
                name: 'from',
                permission: 'active'
              }
            ],
          });
        }
        else {
          this.t.body.push({
            contractName: result.contract,
            selectedAction: result.selectedAction,
            actions: result.actions,
            structs: result.structs,
            authorizations: [
              {
                name: 'from',
                permission: 'active'
              }
            ],
          });
        }
      });
    });
    this.t.authorizationAlly.push({ accounts: [] });
  }
}
