import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
export const API = {
  EOS:'https://api.eosn.io',
  WAX:'https://testnet.wax.eosdetroit.io',
  PROTON:'https://proton.greymass.com',
  TELOS:'https://telos.greymass.com',
  JUNGLE3:'https://api.jungle3.alohaeos.com',
  FIO:'https://fio.greymass.com',
  BOS:'https://api.bossweden.org',
  
  TEST:'https://api.light.xeos.me'
};
@Injectable()
export class ContractApiService {
  private apiIndex = 'EOS';
  constructor(
    private http: HttpClient
  ) {
  }
  public getApiIndex(){
    return `:: ${this.apiIndex}`;
  }
  public changeApi(newApi: string){
    this.apiIndex = newApi;
  }
  getContractActionsList(body: any): Observable<any> {
    return this.http.post<any>(`${API[this.apiIndex]}/v1/chain/get_abi`, body);
  }
  getFromJsonTemplate(){
    return this.http.get('./assets/templates.json');
  }
  getProducers(body: any): Observable<any> {
    return this.http.post<any>(`${API[this.apiIndex]}/v1/chain/get_producers`, body);
  }
  getProducersTEST(from: string): Observable<any> {
    return this.http.get<any>(`${API['TEST']}/api/accinfo/eos/` + from);
  }
}
